.scrollup{
    position: fixed;
    right: 1rem;
    bottom: -20%;
    background-color: var(--first-color-green);
    opacity: .8;
    padding: 0 .3rem;
    border-radius: .4rem;
    z-index: var(--z-tooltip);
    transition: .7s;
}

.scrollup_icon{
    font-size: 1.5rem;
    color: var(--container-color);
}

.scroll-up:hover{
    background-color: var(--first-color-dark);
}

.show-scroll{
    bottom: 5rem;
}
