.header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background: var(--nav-color);
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.05);
}

.li{
  border-radius: 2rem;
}

.a{
  border-radius: 2rem;
}

.nav_link.active {
background-color: var(--header-color);
color: var(--header-text-color);
font-weight: 700;
transition: ease 0.4s;
border-radius: 2rem;
}


/* Estilos para la clase nav_icon_before */
.nav_icon_before {
background-image: var(--logo-1-png);
background-size: contain; /* Ajusta según tus preferencias */
background-repeat: no-repeat; /* Ajusta según tus preferencias */
width: 2rem; /* Ajusta el ancho y alto según tus preferencias */
height: 2rem;
}

.nav{
  display: flex;
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
  font-family: calibri;
}

.nav_menu{
padding: 10px 50px;
}

.nav_logo {
font-family: var(--logo-font);
margin-right: 25rem;
}

.nav_logo,
.nav_toggle{
  color: var(--title-color);
  font-weight: var(--font-medium);
}

.nav_list{
  display: flex;
  column-gap: 1rem;
}

.nav_link{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  transition: .3s;
  padding: 7px 10px;
  cursor: default;
}

.nav_icon,
.nav_close,
.nav_toggle{
display: none;
}


