.nav_icon{
  font-size: 1.2rem;
}

.nav_close{
  position: absolute;
  right: 1.3rem;
  bottom: .5rem;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--title-color);
}

.nav_close:hover{
  color: var(--title-color-dark);
}

.nav_toggle{
  font-size: 1.1rem;
  cursor: pointer;
}

.nav_icon,
.nav_close,
.nav_toggle{
  display: block;
}
}

/*For Small divices*/
@media screen and (max-width: 350px) {
.nav_menu{
  padding: 2rem 0.25rem 4rem;
}

.nav_list{
  column-gap: 0;
}
}